import React from "react";
import axios from "axios";
import Movies from "./Movies";
import "./App.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: [],
  };

  getMovies = async () => {
    const {
      // data.data.movives 자료를 가져옴
      data: {
        data: { movies },
      },
    } = await axios.get(
      "https://yts-proxy.nomadcoders1.now.sh/list_movies.json?sort_by=rating"
    );
    this.setState({ movies, isLoading: false });

    // movies:movies 를 줄여서 movies로 해도됨, 여기서 movies는 state와 axios의 다른 movies
  }; // async 와 await로 시간이 조금 필요하니 기다려달라고 요청 이걸 안쓰면 자바스크립트는 기다리지 않고 지나칠것임

  componentDidMount() {
    // setTimeout(()=>{
    //   this.setState({isLoading: false});
    // }, 6000);
    this.getMovies();
  }

  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading....</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map((movie) => (
              <Movies
                key={movie.id}
                id={movie.id}
                rating={movie.rating}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default App;
